import React, { useState, useMemo, Dispatch, SetStateAction } from 'react';
import { X, Link, RotateCcw, Paperclip, Search, ChevronDown } from 'lucide-react';
import TextareaBox from '../Textarea/TextareaBox';

interface TeamMember {
  id: string;
  name: string;
  role: string;
  avatar: string;
}

interface AddToDoModalProps {
  isOpen: boolean;
  onClose: () => void;
  messageOptions: string[],
  setMessagesOptions: Dispatch<SetStateAction<string[]>>
}

const teamMembers: TeamMember[] = [
  { id: '1', name: 'Naman', role: 'Senior Project Manager', avatar: '/api/placeholder/32/32' },
  { id: '2', name: 'Taarush', role: 'UI/UX Designer', avatar: '/api/placeholder/32/32' },
  { id: '3', name: 'Sansdesh', role: 'Frontend Developer', avatar: '/api/placeholder/32/32' },
  { id: '4', name: 'Prashant', role: 'Frontend Developer', avatar: '/api/placeholder/32/32' },
  { id: '5', name: 'Mr. anonymous', role: 'UI/UX Designer', avatar: '/api/placeholder/32/32' }
];

const NoticeBoardModal = ({ isOpen, onClose, messageOptions, setMessagesOptions }: AddToDoModalProps) => {
  const [selectedAssignee, setSelectedAssignee] = useState<TeamMember | null>(null);
  const [notifyMembers, setNotifyMembers] = useState<TeamMember[]>(teamMembers);
  const [title, setTitle] = useState('');
  const [notes, setNotes] = useState('');
  const [select, setSelect] = useState('');
  const [drop, setDrop] = useState<boolean>(false);
  const [searchTerm, setSearchTerm] = useState('');

  const filteredMembers = useMemo(() => {
    return teamMembers.filter(member =>
      member.name.toLowerCase().includes(searchTerm.toLowerCase())
    );
  }, [searchTerm]);

  const handleMemberClick = (member: TeamMember) => {
    setSelectedAssignee(member);
    if (!notifyMembers.some(m => m.id === member.id)) {
      setNotifyMembers([...notifyMembers, member]);
    }
    setSearchTerm("")
  };

  const removeNotifyMember = (memberId: string) => {
    setNotifyMembers(notifyMembers.filter(member => member.id !== memberId));
  };

  const handleMessageOptionClick = (option: string) => {
    setSelect(option);
    setDrop(false);
  };
  const handleSubmit = () => {
    console.log('Title:', title);
    console.log('Assigned To:', selectedAssignee ? selectedAssignee.name : 'No assignee selected');
    console.log('Notify Members:', notifyMembers.map((member) => member.name));
    console.log('Category:', select);
    console.log('Notes:', notes);
  };
  const [selectOption, setselectOption] = useState("null")

  const [isAddingCategory, setIsAddingCategory] = useState(false);
  const [newCategory, setNewCategory] = useState('');
  if (!isOpen) return null;

  return (
    console.log(messageOptions),
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">

      <div className="bg-white rounded-lg w-full max-w-6xl">
        {/* Header */}
        <div className="flex justify-between items-center p-6 border-b">
          <h2 className="text-xl font-semibold">Add Notice Board Message</h2>
          <button onClick={onClose} className="p-1 hover:bg-gray-100 rounded-full">
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="grid grid-cols-2 gap-6 p-6">
          {/* Left Column */}
          <div className="space-y-6">
            {/* To-Do Description */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Message Title</label>
              <input
                type="text"
                placeholder="Describe..."
                className="w-full p-2 border rounded-lg"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
              />
            </div>
            {/* Category Select */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-[#333333]">Pick a category (optional)</label>
              <div className="w-full">
                {/* className="px-2 py-2 text-sm text-[#333333] border border-gray-200 rounded-lg w-full" */}

                {/* onChange={(e)=> setSelect(e.target.value)} */}
                {/* <select 
                  
                  value={select} 
                >
                  <option value="All" className='hover:bg-[#5D56BD]'>None</option>
                  <option value="Announcement" className='hover:bg-[#5D56BD]'>Announcement</option>
                  <option value="FIY">FIY</option>
                  <option value="Heartbeat">Heartbeat</option>
                  <option value="Pitch">Pitch</option>
                  <option value="Question">Question</option>
                </select> */}
                <div className='flex flex-row w-full'>
                  <button className="flex flex-row w-full px-6 py-2 text-sm border border-gray-200 rounded-lg bg-white" onClick={() => setDrop((prev) => !prev)}>
                    {select || "None"}
                    <ChevronDown className='w-4 h-4 ml-auto' />
                  </button>
                </div>
                {drop && (
                  <div>
                    {messageOptions.map((option) => (
                      <div key={option}>
                        <div
                          className="px-4 py-2 text-sm hover:bg-[#565DBD] hover:text-[white] cursor-pointer"
                          onClick={() => handleMessageOptionClick(option)}
                        >
                          {option}
                        </div>
                      </div>
                    ))}
                    <div>
                      {/* Add categories button */}
                      {!isAddingCategory && (
                        <button
                          className="px-4 text-[#5D56BD] cursor-pointer"
                          onClick={() => setIsAddingCategory(true)}
                        >
                          Add categories
                        </button>
                      )}

                      {/* Input for adding a new category */}
                      {isAddingCategory && (
                        <div className="flex items-center gap-2">
                          <input
                            type="text"
                            className="px-4 py-2 border border-gray-200 rounded-lg"
                            value={newCategory}
                            onChange={(e) => setNewCategory(e.target.value)}
                            placeholder="Enter new category"
                          />
                          <button
                            onClick={() => {
                              if (newCategory.trim()) {
                                setMessagesOptions([...messageOptions, newCategory]);
                                setNewCategory(''); // Reset input field
                                setIsAddingCategory(false); // Close input field
                              }
                            }}
                            className="bg-[#6BBD56] text-white px-3 py-2 rounded-r hover:bg-green-600 ml-auto"
                          >
                            ✔
                          </button>
                          <button
                            onClick={() => {
                              setNewCategory(''); // Reset input field
                              setIsAddingCategory(false); // Close input field
                            }}
                            className="bg-[#FF0000] ml-2 px-3 py-2 text-white hover:bg-red-700"
                          >
                            ✖
                          </button>
                        </div>
                      )}
                    </div>
                  </div>

                )}
              </div>
            </div>

            {/* Notify Members */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-[#333333]">When done, notify</label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Search className="w-4 h-4 text-gray-400" />
                </div>
                <input
                  type="text"
                  placeholder="Search team member"
                  className="w-full p-2 pl-10 border rounded-lg focus:ring-2"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>

              {/* Filtered Member List */}
              {searchTerm && (
                <div className="absolute z-10 w-full bg-white border rounded-lg shadow-lg max-h-60 overflow-y-auto">
                  {filteredMembers.map((member) => (
                    <div
                      key={member.id}
                      className="flex items-center p-2 hover:bg-gray-100 cursor-pointer"
                      onClick={() => handleMemberClick(member)}
                    >
                      <img src={member.avatar} alt="" className="w-8 h-8 rounded-full mr-2" />
                      <div>
                        <div className="font-medium">{member.name}</div>
                        <div className="text-sm text-gray-500">{member.role}</div>
                      </div>
                    </div>
                  ))}
                </div>
              )}

              {/* Selected/Notified Members */}
              <div className="flex flex-wrap gap-2 mt-2">
                {notifyMembers.map((member) => (
                  <div
                    key={member.id}
                    className="flex items-center gap-2 bg-[#5D56BD] text-white px-3 py-1 rounded-full"
                  >
                    <img src={member.avatar} alt="" className="w-6 h-6 rounded-full" />
                    <span>{member.name}</span>
                    <button
                      onClick={() => removeNotifyMember(member.id)}
                      className="hover:bg-purple-200 bg-[#5D56BD] rounded-full p-1"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* Right Column */}
          <div className="space-y-6">
            {/* Notes */}
            <div className="space-y-2">
              <label className="block text-sm font-medium text-gray-700">Notes (Optional)</label>
              <div className="border rounded-lg">
                <div className="flex items-center gap-2 border-b p-2">
                  <button className="p-1 hover:bg-gray-100 rounded">
                    <span className="font-bold">B</span>
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded italic">
                    <span>I</span>
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded">
                    <Link className="w-4 h-4" />
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded">
                    <RotateCcw className="w-4 h-4" />
                  </button>
                  <div className="h-4 w-px bg-gray-300 mx-1" />
                  <button className="p-1 hover:bg-gray-100 rounded">
                    <div className="w-4 h-4 flex flex-col justify-center items-center gap-1">
                      <div className="w-3 h-px bg-gray-600" />
                      <div className="w-3 h-px bg-gray-600" />
                    </div>
                  </button>
                  <button className="p-1 hover:bg-gray-100 rounded">
                    <div className="w-4 h-4 flex flex-col justify-center items-center gap-1">
                      <div className="w-1 h-px bg-gray-600" />
                      <div className="w-3 h-px bg-gray-600 ml-2" />
                    </div>
                  </button>
                  <div className="flex-grow" />
                  <button className="p-1 hover:bg-gray-100 rounded flex items-center gap-1 text-gray-600">
                    <Paperclip className="w-4 h-4" />
                    <span className="text-sm">Attach files</span>
                  </button>
                </div>
                <textarea
                  placeholder="Note..."
                  className="w-full p-2 rounded-b-lg focus:ring-0 focus:outline-none"
                  rows={10}
                  value={notes}
                  onChange={(e) => setNotes(e.target.value)}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Footer */}
        <div className="flex justify-end gap-3 p-6 border-t">
          <button
            onClick={handleSubmit}
            className="px-4 py-2 text-white bg-[#5D56BD] rounded-lg"
          >
            Post this message
          </button>
          <button
            onClick={onClose}
            className="px-4 py-1 border-[#6BBD56] border-2 text-[#6BBD56] rounded-lg hover:bg-green-600 hover:text-white"
          >
            Save a draft
          </button>
        </div>
      </div>

    </div>
  );
};

export default NoticeBoardModal;