import React, { useState } from 'react';
import { X } from 'lucide-react';

interface ProjectFormData {
  name: string;
  category: string;
  about: string;
  collaboration: string;
}

interface EditProjectModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const EditProjectModal: React.FC<EditProjectModalProps> = ({
  isOpen,
  onClose,
}) => {
  const defaultValues: ProjectFormData = {
    name: 'Project Atlas',
    category: 'development',
    about: 'Project Atlas aims to develop an innovative project management tool designed to streamline workflows and enhance team collaboration.',
    collaboration: 'Collaboration is at the heart of Project Atlas, ensuring seamless teamwork and communication.',
  };

  const [formData, setFormData] = useState<ProjectFormData>(defaultValues);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { id, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [id]: value,
    }));
  };

  const handleSave = async (e: React.FormEvent) => {
    e.preventDefault();
    console.log('Saving:', formData);
    onClose();
  };

  const handleDelete = async () => {
    if (window.confirm('Are you sure you want to delete this project?')) {
      console.log('Deleting project');
      onClose();
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black/60 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg shadow-2xl w-full max-w-7xl relative">
        {/* Close Button */}
        <button
          onClick={onClose}
          className="absolute right-4 top-4 hover:bg-gray-200 p-2 rounded-full"
          type="button"
          aria-label="Close modal"
        >
          <X className="h-6 w-6 text-gray-500" />
        </button>
        {/* Modal Content */}
        <h2 className="text-2xl font-semibold text-gray-800 mb-6 pl-5 pt-5">Edit Project</h2>
        <div className="flex items-center justify-center">
          <div className="w-24 h-24 bg-red-300 rounded-full flex items-center justify-center mb-4">
            <span className="text-white text-4xl font-semibold">PA</span>
          </div>
        </div>
        <div className="p-8">
          <form onSubmit={handleSave} className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {/* Left Column */}
            <div className="space-y-6">
              <div>
                <label htmlFor="name" className="block text-sm font-medium text-gray-700 mb-2">
                  Project Name
                </label>
                <input
                  id="name"
                  type="text"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={formData.name}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div>
                <label htmlFor="about" className="block text-sm font-medium text-gray-700 mb-2">
                  About Project
                </label>
                <textarea
                  id="about"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 min-h-[160px]"
                  value={formData.about}
                  onChange={handleInputChange}
                  required
                />
              </div>
            </div>
            <div className="space-y-6">
              <div>
                <label htmlFor="category" className="block text-sm font-medium text-gray-700 mb-2">
                  Project Category
                </label>
                <select
                  id="category"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={formData.category}
                  onChange={handleInputChange}
                >
                  <option value="development">Project Development</option>
                  <option value="DevOps">DevOps</option>
                  <option value="Mobile">Mobile Development</option>
                  <option value="Web">Web Development</option>
                  <option value="CRM">Customer Relationship Management (CRM)</option>
                  <option value="ERP">Enterprise Resource Planning (ERP)</option>
                  <option value="AI">Artificial Intelligence (AI)</option>
                </select>
              </div>

              <div>
                <label htmlFor="collaboration" className="block text-sm font-medium text-gray-700 mb-2">
                  How we collaborate?
                </label>
                <textarea
                  id="collaboration"
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 min-h-[160px]"
                  value={formData.collaboration}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div className="flex justify-end gap-4">
                <button
                  type="button"
                  onClick={handleDelete}
                  className="px-6 py-3 text-white bg-[#FF0000] hover:bg-red-600 rounded-lg "
                >
                  Delete Project
                </button>
                <button
                  type="submit"
                  className="px-6 py-3 text-white bg-[#5D56BD] hover:bg-blue-600 rounded-lg "
                >
                  Save Changes
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default EditProjectModal;
